import React, { useState, useEffect, Component } from "react";
import Button from "@material-ui/core/Button";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import TextField from "@material-ui/core/TextField";
import { useHistory } from "react-router-dom";
import DataSharingService from "../helpers/dataShare";
import CommunicationService from "../helpers/communicationService";
const config = require("../helpers/config.json");

export default class Register extends Component<any, any> {
  render() {
    return (
      <div
        style={{
          height: "100vh",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          background: "linear-gradient(-45deg,#067AD9, #12848B)",
        }}
      >
        <Card
          style={{
            minWidth: "30%",
            borderRadius: "15px",
          }}
        >
          <CardContent
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <h1>Register New</h1>
            <TextField
              style={{ margin: 5, width: "20em" }}
              id="register-username"
              label="Username"
              variant="outlined"
              //   value={userName}
              //   onChange={(event) => setUserName(event.target.value)}
            />
            <TextField
              style={{ margin: 5, width: "20em" }}
              id="register-password"
              label="Password"
              variant="outlined"
              //   value={password}
              type="password"
              //   onChange={(event) => setPassword(event.target.value)}
            />
            <Button
              variant="contained"
              //   onClick={() => onRegister()}
              color="primary"
            >
              Register
            </Button>
            <div style={{ width: "100%" }}>
              <p
                style={{
                  fontSize: "14px",
                  fontWeight: 600,
                  color: "#3f51b5",
                  padding: 0,
                  margin: 0,
                  textAlign: "end",
                  cursor: "pointer",
                }}
                onClick={() => {
                  //   setIsRegister(false);
                }}
              >
                login?
              </p>
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }
}
